.header_container{
	position:fixed;
    height:61px;
	width:100%;
	top:0;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.flex{
    display:-ms-flexbox;
    display:flex;
}
.row{
    width: 100%;
    height: 100%;
    background-color:  #2C2C37;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.menu_icon{
    display:none;
    margin-left:15px;
    cursor:pointer;
}
h1{
	font-size:3em;
}
.icon_item{
    width: 25px;
    height: 2px;
    background-color: #fff;
    margin: 4px 0;
    border-radius:5px;
}
.header{
    width:90%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}
.logo{
    color:#fff;
}
@media (max-width:1030px) {

    .menu_icon{
        display: block;
    }
    
    .logo{
        margin-left:25px;
    }
    .user{
        width:60%;
    }
    .list{
        -ms-flex-pack: center;
            justify-content: center;
    }
	.list_item:nth-child(2){
		display:none;
	}
	h1{
		font-size:25px;
	}
}



