*{
    margin: 0;
    padding: 0;
}
html,body{
	height:100%;
    /*background-color: #f1f1f1;*/
}

@font-face {
    font-family: font;
    src: url(Quicksand-Regular.ttf);
}
#root{
	height: 100%;
}
main{
    width:85%;
    height: 90%;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align: center;
        align-items: center;
    float: left;
	margin-left:15%;
	margin-top:61px;
    }
a{
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    
}

 
@media (max-width:1030px) {

    main{
        width: 100%;
		margin-left:0;
    }
}



