table {
    border-collapse: collapse;
    width: 100%;
}
th, td {
    padding: 1%;
    text-align: left;
}
.UserTable {
	width: 100%;
   	height: 100%;
    color: #513654;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
	padding:1.1%;
	overflow: auto;
} 
.TableContainer{
	height: 90%;
}
.table tr:nth-child(even){
	background-color: #f2f2f2; 
}
.table {
    width: 100%;
    margin: 0px auto;
    border-collapse: collapse;
}
.table td{
	border-collapse: collapse;
	text-align: left;
}
.table th{
	border-collapse: collapse;
	color: azure;
    background: #1A1F25;
}
button:disabled{ 
  background-color: #cccccc !important;
  color: #A9A9A9 !important;
  pointer-events: none;

}
.buttons button{
	outline: none;
	-webkit-transition: 2s;
	-o-transition: 2s;
	transition: 2s;
    color: #fff;
    background-color: #1A1F25;
    border-radius: .1rem;
	border: 0px solid transparent;
	cursor:pointer;
	width: 150px;
	float: left;
	margin: 1% 1% 0 1%;
	padding: 1%;
}
button:hover{
	 background-color: rgb(40, 47, 71); 
	 -webkit-transition: 1s; 
	 -o-transition: 1s; 
	 transition: 1s;
}
.fa-edit{
	cursor: pointer;
	font-size:25px;
}
.addtablerow{
	width: 100%;
}

.AddRow{
	display:inline-block;
    margin-top: 10px;
}
.check{
	float: left;
	margin-top: 5px;
	margin-left: 20px;
}
.check{
	margin-left:39%;
	margin-top: 2px;
}
.templateForm{
	height: 290px;
    overflow: auto;
	margin: 0 5%;
}
.template{
	background: rgba(44, 44, 55, 0.9);
	width: 300px;
	height: 380px;
	margin: auto auto;
	line-height: 2;
	text-align: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.popup_p {
	font-size: 1.5em;
	font-style: italic;
	margin: 3% auto;
	color: #F2F2F2;
	text-align: center;
	width: 78%;
}
.templateSpan{
	font-size: 1.2em;
}
.block{
	position: fixed;
	left: 0; 
	top: 0;
	bottom: 0;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	background-color: rgba(44, 44, 55, 0.4);
}
.template button,.ex_mailinglist_btn,.upload_btn {
	width: 60px;
	margin-left: 5%;
	color: #1A1F25;
    background-color: #F9F4F5;
	font-weight: bold;
    border-radius: .1rem;
	border: 0px solid transparent;
	height: 30px;
	cursor:pointer;
	-webkit-box-shadow: 0 0px 0px 0 rgba(40, 47, 71,0.4);
	        box-shadow: 0 0px 0px 0 rgba(40, 47, 71,0.4);
	padding: 1%;
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
	outline: none;
}
.ex_mailinglist_btn:hover,.delete_popoUp button:hover,.mailing_list_form button:hover,.template button:hover{
	background: #1A1F25;
	color: azure;
	
}
input[type="radio"]{
    display:none;
}
input[type="radio"]:checked+span{
    background: #1A1F25;
}
.templateSpan{
	cursor: pointer;
	display: block;
	color: azure;
	padding: 1%;
}
.templateSpan:hover{
	background: #1A1F25;
}
.mailing_list_form{
	background: rgba(44, 44, 55, 0.9);
	width: 400px;
	height: 200px;
	margin: auto auto;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: -ms-flexbox;
	display: flex;
}
.mailing_list_form button{
	color: #1A1F25;
    background-color: #F9F4F5;
	font-weight: bold;
	height: 25px;
    border-radius: .1rem;
	cursor:pointer;
	padding: 1%;
	margin-top: 11%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	width: 47.5%;
	margin-left: 0;
}
#mail_btn_cancel{
	margin-right: 0;
}
#btn_create{
	margin-right: 5%;
}

.mailing_list_input{
	margin: auto auto;
}
.mailing_list_input p{
	width: 100%;
	color:azure;
}
.delete_popoUp{
	width: 300px;
    height: 150px;
    background: rgba(44, 44, 55, 0.9);
	margin: auto auto;
	color: #F9F4F5;
	padding: 5px;
	text-align: center;
}
.delete_popoUp p {
	margin: 11% 0 11% 0;
}
.delete_popoUp button {
	width: 80px;
	margin: 0 12% 0 0;
	color: #1A1F25;
    background-color: #F9F4F5;
	font-weight: bold;
	height: 25px;
    border-radius: .1rem;
	outline: none;
}
#popup_delete {
	margin-left: 16.5%;
}
.upload_form{
	background: rgba(44, 44, 55, 0.9);
	color: azure;
	width: 350px;
	margin: auto auto;
	height: 200px;
	display: -ms-flexbox;
	display: flex;
}
.upload_container{
	margin: auto auto;
}
.upload_btn{
	width: 98px !important;
	margin: 40px 20px 0 2px !important;
}
.upload_btn:hover{
	background: #1A1F20;
	color: azure;
}
.edit_block{
	background: rgba(44, 44, 55, 0.06) !important;
}
.edit_form_block{
	background: rgba(44, 44, 55, 0.9);
	color: azure;
	width: 400px;
	margin: auto auto;
	height: 290px;
	line-height: 2;
	text-align: left;
	display: -ms-flexbox;
	display: flex;
}
.edit_form{
	width: 80%;
	margin: auto auto;
}
.add_contact_value,.edit_value{
	width: 110px;
	display: inline-block;
}
.edit_input,.add_input{
	color:#85858A;
	outline: none;
	border-radius: 3px;
	padding: 1%;
	margin: 2px;
}
.edit_btn,.add_btn{
	outline: none;
	width: 60px;
    background-color: #F9F4F5;
	font-weight: bold;
    border-radius: .1rem;
	border: 0px solid transparent;
	padding: 2%;
	cursor:pointer;
	-webkit-box-shadow: 0 ;
	        box-shadow: 0 ;
	margin: 7% 5% 0 0;
}
.add_btn:hover,.edit_btn:hover{
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
	background: #1A1F25;
	color: azure;
}
#edit_td{
	text-align: center;
}
.add_new_contact_form label{
	line-height: 2; 
}
.add_new_contact_form{
	background: rgba(44, 44, 55, 0.9);
	color: azure;
	width: 400px;
	margin: auto auto;
	height: 300px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: -ms-flexbox;
	display: flex;
}
.add_contact_content{
	width: 90%;
	margin: auto auto;
}
.add_input{
	color: black !important;
}
.subscribingMailigList{
	background: rgba(44, 44, 55, 0.9);
	width: 300px;
	height: 380px;
	margin: auto auto;
	line-height: 2;
	text-align: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.existing_mailing_list{
	width: 80%;
	margin: 5% auto;
}
.mailingLIsts{
	height: 300px;
    overflow-y: auto;
    margin: 0 auto;
}
.error{
	color: #ea4335;
	font-size: 0.8em;
}
@media (max-width:768px) {
	.UserTable{
		width:100%;
	}
}
@media(max-height:400px){
	.template{
		height: 250px;
	}
	.templateForm{
		height: 170px;
	}
}
@media (max-width:468px) {
	#theader{
		height: 30px;
		text-align: center;
		font-size: 1.5em;
		}
}
@media (max-width:618px) {
	.buttons{
		width: 45%;
	}
}
@media (max-width:482px) {
	.mailing_list_form{
		width: 100%;
	}
	.upload_form{
		width: 100%
	}
}
@media (max-width:385px) {
	.add_new_contact_form{
		height: auto;
	}
	.edit_form_block{
		height: auto;
	}
	.edit_btn{
		margin-bottom: 2%;
	}
}
.empty_contacts{
	text-align: center
}



